import React, { useContext, useState } from "react";
import "./Portfolio.css"; // Import your CSS styles here
import myPhoto from "../images/myPhoto2.png";
import { myTheme } from "../Context/Theme";
import Navbar from "../Pages/Navbar";
import About from "../Pages/About";
import Skill from "../Pages/Skill";
import Project from "../Pages/Project";
import GitState from "../Pages/GitState";
import GitCalender from "../Pages/GitCalender";
import Contact from "../Pages/Contact";
const Portfolio = () => {
  const {isAuth , setIsAuth} = useContext(myTheme)
  const handleResumeButtonClick = () => {
    window.open(
      "https://drive.google.com/file/d/19-r_RcQ0FfFn8t6aDaZzktGJ6scJx2HB/view",
      "_blank"
    );
  };
  function handleTheme() {
    if(isAuth){
      setIsAuth(false)
    }else{
      setIsAuth(true);

    }
  }
  return (<>
  
    <div style={isAuth?{backgroundColor: "#102c47" , color :"#ffc107"}:{backgroundColor:"whitesmoke"}}>
      {/* Navbar */}


      {/* Home Section */}
      <Navbar handleResumeButtonClick={handleResumeButtonClick} handleTheme={handleTheme} />
      <section id="home">
        <img src={myPhoto} alt="Your Photo" className="home-img" />
        <h1 id="user-detail-name" className="text-warning">Saif Khan</h1>
        <p className="theme-color home-color">Front-End Developer</p>
      </section>

      {/* About Section */}
      <About handleResumeButtonClick={handleResumeButtonClick}/>

      {/* Skills Section */}
      <Skill/>
      {/* Projects Section */}
      <Project/>



      {/* GitHub Calendar and Stats */}
      <GitCalender/>
     <GitState/>
           {/* Contact Section */}
           <section id="contact"  style={isAuth?{backgroundColor: "#102c47" , color :"#ffc107"}:{}}>
           <h1 className="mb-5 text-center">Contact Me</h1>
      {/* <a href="GitHub-profile-link" id="contact-github">
        GitHub
      </a>
      <a href="LinkedIn-profile-link" id="contact-linkedin">
        LinkedIn
      </a>
      <p id="contact-phone">Your Phone Number</p>
      <p id="contact-email">Your Email Address</p> */}
      <Contact/>
    </section>
    </div>
  </>
  );
};

export default Portfolio;
